.opnbtn:hover{
    cursor:pointer;
    opacity: 0.95;
    height: 6vh;
}
.opnbtn{
    height: 4vh;
    border-radius: 10px 10px 0 0;
    float: right;
    margin-right: 30px;
    transition: 0.2s;
    border-bottom: 0;
    background-color: white;
    color: black;
}

@media screen and (min-width: 320px) {
    .opnbtn {
      width: 25vw;
    }
  }
  @media screen and (min-width: 1000px) {
    .opnbtn {
      width: 15vw;
      opacity: 0.75;
    }
  }