

.App {
  background-color:#52d19c;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: white;
}

input[type=text], select, textarea, date {
  width: 30%;
  padding: 12px;
  border: 2px solid black;
  border-radius: 16px;
  box-sizing: border-box;
  resize: vertical;
  margin-bottom: 10px;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}


ul {
  list-style-type: none;
  padding: 0px;
}

li{
  margin: 20px;
}

.results {
  width: 40%;

  margin: 0;
}

.content{
  display: flex;
  bottom: 0;
  min-height: 80vh;
  width: 100vw;
  justify-content: center;
  margin-top:20vh;
}

img{
  height: 150px;
  width: auto;
  margin: 5px;
}

.searchHeader{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-height: 20vh; 
  margin: 0;
  background-color: #101318;
  width: 100%;
  text-align: center;
  position: fixed;
  top: 0px;
  z-index: 1;
}

.shortlist{
  position: fixed;
  bottom: 0;
  width: 100vw;
}


@media screen and (max-width: 992px){
  .results{
    width: 80%;
  }
  input[type=text], select, textarea, date {
    width: 80%;
  }
}

@media screen and (min-width: 320px) {
  .searchHeadertext {
    font-size: calc(16px + 6 * ((100vw - 320px) / 680));
  }
}
@media screen and (min-width: 1000px) {
  .searchHeadertext {
    font-size: 22px;
  }
}
